import {useEffect, useState} from 'react';
import usePolyDogeFinance from './usePolyDogeFinance';
import useRefresh from './useRefresh';

const useTotalValueLocked = () => {
  const [totalValueLocked, setTotalValueLocked] = useState<Number>(0);
  const {slowRefresh} = useRefresh();
  const polydogeFinance = usePolyDogeFinance();

  useEffect(() => {
    async function fetchTVL() {
      try {
        setTotalValueLocked(await polydogeFinance.getTotalValueLocked());
      } catch (err) {
        console.error(err);
      }
    }
    fetchTVL();
  }, [setTotalValueLocked, polydogeFinance, slowRefresh]);

  return totalValueLocked;
};

export default useTotalValueLocked;
