/* eslint-disable */
import React, { useMemo } from 'react';
import Page from '../../components/Page';
import { createGlobalStyle } from 'styled-components';
import CountUp from 'react-countup';
import TokenSymbol from '../../components/TokenSymbol';
import usePDDollarStats from '../../hooks/usePDDollarStats';
import useLpStats from '../../hooks/useLpStats';
import useLpStatsMATIC from '../../hooks/useLpStatsMATIC';
import usePolyDogestats from '../../hooks/usePolyDogestats';
import useModal from '../../hooks/useModal';
import useZap from '../../hooks/useZap';
import useBondStats from '../../hooks/useBondStats';
import useaShareStats from '../../hooks/usePDShareStats';
import useTotalValueLocked from '../../hooks/useTotalValueLocked';
import {
  PDDollar as pddollarProd,
  PDShare as aShareProd,
} from '../../polydoge-finance/deployments/deployments.mainnet.json';
import { roundAndFormatNumber } from '../../0x';
import MetamaskFox from '../../assets/img/metamask-fox.svg';
import { Box, Button, Grid } from '@material-ui/core';
import ZapModal from '../Bank/components/ZapModal';
import { makeStyles } from '@material-ui/core/styles';
import usePolyDogeFinance from '../../hooks/usePolyDogeFinance';
import PDDollarImage from '../../assets/img/pddollar-animated.png';
import SafeImage from '../../assets/img/safe_icon.png';
import HomeImage from '../../assets/img/background.png';
import { ReactComponent as IconLink } from '../../assets/img/external-link.svg';
import {ReactComponent as IconTwitter} from '../../assets/img/twitter.svg';
import './home.css';
const BackgroundImage = createGlobalStyle`
  body {
    background-image: url(${HomeImage});
  }
`;

const useStyles = makeStyles((theme) => ({
  button: {
    [theme.breakpoints.down('415')]: {
      // marginTop: '10px'
    },
  },
}));

const Home = () => {
  const classes = useStyles();
  const TVL = useTotalValueLocked();
  const pddollarFtmLpStats = useLpStatsMATIC('PDDOLLAR-WMATIC-LP');
  const pdogeFtmLpStats = usePolyDogestats();
  const aShareFtmLpStats = useLpStats('PDSHARE-ETH-LP');
  const pddollarStats = usePDDollarStats();
  const aShareStats = useaShareStats();
  const tBondStats = useBondStats();
  const polydogeFinance = usePolyDogeFinance();
  const pddollar = pddollarProd;
  const aShare = aShareProd;
  const buyPDDollarAddress =
    'https://quickswap.exchange/#/swap?inputCurrency=0x0d500B1d8E8eF31E21C99d1Db9A6444d3ADf1270&outputCurrency=' +
    pddollar.address;
  const buyPDShareAddress = 'https://quickswap.exchange/#/swap?outputCurrency=' + aShare.address;
  const pddollarLPStats = useMemo(() => (pddollarFtmLpStats ? pddollarFtmLpStats : null), [pddollarFtmLpStats]);
  const pdogeLPStats = useMemo(() => (pdogeFtmLpStats ? pdogeFtmLpStats : null), [pdogeFtmLpStats]);
  const bshareLPStats = useMemo(() => (aShareFtmLpStats ? aShareFtmLpStats : null), [aShareFtmLpStats]);
  const pddollarPriceInDollars = useMemo(
    () => (pddollarStats ? Number(pddollarStats.priceInDollars).toFixed(4) : null),
    [pddollarStats],
  );
  const pddollarPriceInETH = useMemo(
    () => (pddollarStats ? Number(pddollarStats.tokenInFtm).toFixed(4) : null),
    [pddollarStats],
  );
  // console.log('pddollarprice', pddollarPriceInETH);
  const pddollarCirculatingSupply = useMemo(
    () => (pddollarStats ? String(pddollarStats.circulatingSupply) : null),
    [pddollarStats],
  );
  const pddollarTotalSupply = useMemo(
    () => (pddollarStats ? String(pddollarStats.totalSupply) : null),
    [pddollarStats],
  );
  const aSharePriceInDollars = useMemo(
    () => (aShareStats ? Number(aShareStats.priceInDollars).toFixed(2) : null),
    [aShareStats],
  );
  const aSharePriceInETH = useMemo(
    () => (aShareStats ? Number(aShareStats.tokenInFtm).toFixed(4) : null),
    [aShareStats],
  );
  const aShareCirculatingSupply = useMemo(
    () => (aShareStats ? String(aShareStats.circulatingSupply) : null),
    [aShareStats],
  );
  const aShareTotalSupply = useMemo(() => (aShareStats ? String(aShareStats.totalSupply) : null), [aShareStats]);

  const tBondPriceInDollars = useMemo(
    () => (tBondStats ? Number(tBondStats.priceInDollars).toFixed(2) : null),
    [tBondStats],
  );
  const tBondPriceInETH = useMemo(() => (tBondStats ? Number(tBondStats.tokenInFtm).toFixed(4) : null), [tBondStats]);
  const tBondCirculatingSupply = useMemo(
    () => (tBondStats ? String(tBondStats.circulatingSupply) : null),
    [tBondStats],
  );
  const tBondTotalSupply = useMemo(() => (tBondStats ? String(tBondStats.totalSupply) : null), [tBondStats]);
  const pddollarLpZap = useZap({ depositTokenName: 'PDDOLLAR-WMATIC-LP' });
  const bshareLpZap = useZap({ depositTokenName: 'PDSHARE-ETH-LP' });

  const [onPresentPDDollarZap, onDissmissPDDollarZap] = useModal(
    <ZapModal
      decimals={18}
      onConfirm={(zappingToken, tokenName, amount) => {
        if (Number(amount) <= 0 || isNaN(Number(amount))) return;
        pddollarLpZap.onZap(zappingToken, tokenName, amount);
        onDissmissPDDollarZap();
      }}
      tokenName={'PDDOLLAR-WMATIC-LP'}
    />,
  );

  const [onPresentPDshareZap, onDissmissPDshareZap] = useModal(
    <ZapModal
      decimals={18}
      onConfirm={(zappingToken, tokenName, amount) => {
        if (Number(amount) <= 0 || isNaN(Number(amount))) return;
        bshareLpZap.onZap(zappingToken, tokenName, amount);
        onDissmissPDshareZap();
      }}
      tokenName={'PDSHARE-ETH-LP'}
    />,
  );

  return (
    <Page>
      <BackgroundImage />
      <Grid container className="mainGrid">
        <Grid item xs={12}>
          <div className="tokenButtonsContainer">
            <div className="tokenButtons">
              <Button href="/boardroom" className="shinyButtonUI fuxiaButton">
                Stake Now
              </Button>
              <Button href="/farm" className="shinyButtonUI violetButton">
                Farm Now
              </Button>
              <Button target="_blank" href={buyPDDollarAddress} className={'shinyButtonUI yellowButton'}>
                Buy PDDOLLAR
              </Button>
              <Button target="_blank" href={buyPDShareAddress} className={'shinyButtonUI lightPurpleButton'}>
                Buy PDSHARE
              </Button>
            </div>
          </div>
        </Grid>
        <Grid className="ballerzBox" item xs={12}>
          <img className="ballerzImg" src={PDDollarImage} />
          <div className="ballerzTextBox">
            <Box className="ballerzText">
              <h2>PolyDogeDAO turns puppies into ballerz!</h2>
              <p>
                Product of{' '}
                <a href="https://polydoge.com" rel="noopener" target="_blank">
                  PolyDoge Ecosystem
                </a>
                . Algorithmic coin on the Polygon blockchain.
              </p>
              <p className="yellowText textBigger">$PDDOLLAR has variable expansion based on ratio to MATIC.</p>
              <div className="communityLinks">
                <Button
                  href="https://discord.link/MetaPetzAI"
                  className="shinyButton"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  Join Community
                </Button>
                <Button
                  href="https://polydogedao.gitbook.io/"
                  className="shinyButton bGradient"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  Read Documentation
                </Button>
                <a
                  href="https://twitter.com/PolyDoge"
                  rel="noopener noreferrer"
                  target="_blank"
                  className="twitterIconHome"
                >
                  <IconTwitter />
                </a>
              </div>
            </Box>
            <div className="tvlAll">
              <div className="tvlAllContent cardContainer">
                <img className="safeImg" src={SafeImage} />
                <h3>
                  PolyDogeDAO TVL
                  <br />
                  Total Value Locked
                </h3>
                <CountUp className="tvlCount" end={TVL} separator="," prefix="$" />
              </div>
            </div>
          </div>
        </Grid>
        {/* PDDOLLAR */}
        <Grid item xs={12} md={6}>
          <div className="tokenCard yellowText">
            <div className="cardContainer">
              <div className="tokenBox">
                <TokenSymbol symbol="PDDOLLAR" />
                <div className="infoToken">
                  <h4>PDD</h4>
                  <span>(PolyDogeDollar)</span>
                  <a
                    href="https://polygonscan.com/address/0x146e58D34EaB0bFf7e0a63cfe9332908d680c667#code"
                    rel="noopener noreferrer"
                    target="_blank"
                  >
                    Show on Polygonscan <IconLink />
                  </a>
                </div>
              </div>
              <Button
                className="metaMaskLink"
                onClick={() => {
                  polydogeFinance.watchAssetInMetamask('PDDOLLAR');
                }}
              >
                <img alt="metamask fox" src={MetamaskFox} />
              </Button>
              <span className="currentPriceTitle">Current price</span>
              <div className="currentPriceBox">
                <span className="yellowText">{pddollarPriceInETH ? pddollarPriceInETH : '-.----'} MATIC</span>
                <span>${pddollarPriceInDollars ? roundAndFormatNumber(pddollarPriceInDollars, 2) : '-.--'}</span>
              </div>
              <ul className="supplyInfo">
                <li>
                  Market Cap:{' '}
                  <span>${roundAndFormatNumber(pddollarCirculatingSupply * pddollarPriceInDollars, 2)}</span>
                </li>
                <li>
                  Circulating Supply: <span>{roundAndFormatNumber(pddollarCirculatingSupply, 2)}</span>
                </li>
                <li>
                  Total Supply: <span>{roundAndFormatNumber(pddollarTotalSupply, 2)}</span>
                </li>
              </ul>
              <div className="buyFarmToken">
                <Button
                  href="https://quickswap.exchange/#/swap?outputCurrency=0x146e58D34EaB0bFf7e0a63cfe9332908d680c667"
                  className="shinyButtonUI yellowButton"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  Buy
                </Button>
                <Button href="/farm" className="shinyButtonUI purpleButton">
                  Farm
                </Button>
              </div>
            </div>
          </div>
        </Grid>
        {/* PDSHARE */}
        <Grid item xs={12} md={6}>
          <div className="tokenCard lillaText">
            <div className="cardContainer">
              <div className="tokenBox">
                <TokenSymbol symbol="PDSHARE" />
                <div className="infoToken">
                  <h4>PDS</h4>
                  <span>(PolyDogeShare)</span>
                  <a
                    href="https://polygonscan.com/address/0x3068382885602FC0089aeC774944b5ad6123ae60#code"
                    rel="noopener noreferrer"
                    target="_blank"
                  >
                    Show on Polygonscan <IconLink />
                  </a>
                </div>
              </div>
              <Button
                className="metaMaskLink"
                onClick={() => {
                  polydogeFinance.watchAssetInMetamask('PDSHARE');
                }}
              >
                <img alt="metamask fox" src={MetamaskFox} />
              </Button>
              <span className="currentPriceTitle">Current price</span>
              <div className="currentPriceBox">
                <span className="lillaText">{aSharePriceInETH ? aSharePriceInETH : '-.----'} ETH</span>
                <span>${aSharePriceInDollars ? aSharePriceInDollars : '-.--'}</span>
              </div>
              <ul className="supplyInfo">
                <li>
                  Market Cap:{' '}
                  <span>${roundAndFormatNumber((aShareCirculatingSupply * aSharePriceInDollars).toFixed(2), 2)} </span>
                </li>
                <li>
                  Circulating Supply: <span>{roundAndFormatNumber(aShareCirculatingSupply, 2)}</span>
                </li>
                <li>
                  Total Supply: <span>{roundAndFormatNumber(aShareTotalSupply, 2)}</span>
                </li>
              </ul>
              <div className="buyFarmToken">
                <Button
                  href="https://quickswap.exchange/#/swap?outputCurrency=0x3068382885602FC0089aeC774944b5ad6123ae60"
                  className="shinyButtonUI lightPurpleButton"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  Buy
                </Button>
                <Button href="/boardroom" className="shinyButtonUI purpleButton">
                  Farm
                </Button>
              </div>
            </div>
          </div>
        </Grid>
        {/* PDBOND */}
        {/* <Grid item xs={12} md={4}>
          <div className="tokenCard greenText">
            <div className="cardContainer">
              <div className="tokenBox">
                <TokenSymbol symbol="PDBOND" />
                <div className="infoToken">
                  <h4>PDB</h4>
                  <span>(PolyDogeBond)</span>
                  <a
                    href="https://polygonscan.com/address/0x6b6969E2C4Db7c5989EE36610405D894Cd5aC9d7#code"
                    rel="noopener noreferrer"
                    target="_blank"
                  >
                    Show on Polygonscan <IconLink />
                  </a>
                </div>
              </div>
              <Button
                className="metaMaskLink"
                onClick={() => {
                  polydogeFinance.watchAssetInMetamask('PDBOND');
                }}
              >
                <img alt="metamask fox" src={MetamaskFox} />
              </Button>
              <span className="currentPriceTitle">Current price</span>
              <div className="currentPriceBox">
                <span className="greenText">{tBondPriceInETH ? tBondPriceInETH : '-.----'} MATIC</span>
                <span>${tBondPriceInDollars ? tBondPriceInDollars : '-.--'}</span>
              </div>
              <ul className="supplyInfo">
                <li>
                  Market Cap:{' '}
                  <span>${roundAndFormatNumber((tBondCirculatingSupply * tBondPriceInDollars).toFixed(2), 2)}</span>
                </li>
                <li>
                  Circulating Supply: <span>{roundAndFormatNumber(tBondCirculatingSupply, 2)}</span>
                </li>
                <li>
                  Total Supply: <span>{roundAndFormatNumber(tBondTotalSupply, 2)}</span>
                </li>
              </ul>
              <div className="buyFarmToken">
                <Button href="/bond" className="shinyButtonUI lightGreenButton">
                  Bond
                </Button>
              </div>
            </div>
          </div>
        </Grid> */}
        <Grid item xs={12} sm={6}>
          <div className="tokenCard pair">
            <div className="cardContainer">
              <div className="tokenBox">
                <TokenSymbol symbol="PDDOLLAR-WMATIC-LP" />
                <div className="pricePair">
                  <h2>PDDOLLAR-WMATIC QuickSwap LP</h2>
                  <span>Price: ${pddollarLPStats?.priceOfOne ? pddollarLPStats.priceOfOne : '-.--'}</span>
                  <span>
                    {pddollarLPStats?.tokenAmount ? pddollarLPStats?.tokenAmount : '-.--'} PDDOLLAR /{' '}
                    {pddollarLPStats?.ftmAmount ? pddollarLPStats?.ftmAmount : '-.--'} MATIC
                  </span>
                </div>
              </div>
              <div>
                <Grid className="infoPair" container>
                  {/* <Grid className='infoPairTab' item xs={12} md={4}>
                    Farming APR
                    <span></span>
                  </Grid> */}
                  <Grid className="infoPairTab" item xs={12} md={6}>
                    Liquidity:
                    <span>
                      $
                      {pddollarLPStats?.totalLiquidity
                        ? roundAndFormatNumber(pddollarLPStats.totalLiquidity, 3)
                        : '-.--'}{' '}
                    </span>
                  </Grid>
                  <Grid className="infoPairTab" item xs={12} md={6}>
                    Total Supply:
                    <span>
                      {pddollarLPStats?.totalSupply ? roundAndFormatNumber(pddollarLPStats.totalSupply, 3) : '-.--'}
                    </span>
                  </Grid>
                </Grid>
              </div>
              <div>
                <Button
                  href="https://quickswap.exchange/#/add/ETH/0x146e58D34EaB0bFf7e0a63cfe9332908d680c667"
                  className="shinyButtonUI yellowButton"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  Add LP
                </Button>
              </div>
            </div>
          </div>
        </Grid>
        <Grid item xs={12} sm={6}>
          <div className="tokenCard pair">
            <div className="cardContainer">
              <div className="tokenBox">
                <TokenSymbol symbol="PDSHARE-ETH-LP" />
                <div className="pricePair">
                  <h2>PDSHARE-ETH QuickSwap LP</h2>
                  <span>Price: ${bshareLPStats?.priceOfOne ? bshareLPStats.priceOfOne : '-.--'}</span>
                  <span>
                    {bshareLPStats?.tokenAmount ? bshareLPStats?.tokenAmount : '-.--'} PDSHARE /
                    {bshareLPStats?.ftmAmount ? bshareLPStats?.ftmAmount : '-.--'} ETH
                  </span>
                </div>
              </div>
              <div>
                <Grid className="infoPair" container>
                  {/* <Grid className='infoPairTab' item xs={12} md={4}>
                    Farming APR
                    <span></span>
                  </Grid> */}
                  <Grid className="infoPairTab" item xs={12} md={6}>
                    Liquidity:
                    <span>
                      ${bshareLPStats?.totalLiquidity ? roundAndFormatNumber(bshareLPStats.totalLiquidity, 2) : '-.--'}
                    </span>
                  </Grid>
                  <Grid className="infoPairTab" item xs={12} md={6}>
                    Total Supply:
                    <span>
                      {bshareLPStats?.totalSupply ? roundAndFormatNumber(bshareLPStats.totalSupply, 2) : '-.--'}
                    </span>
                  </Grid>
                </Grid>
              </div>
              <div>
                <Button
                  href="https://quickswap.exchange/#/add/0x7ceB23fD6bC0adD59E62ac25578270cFf1b9f619/0x3068382885602FC0089aeC774944b5ad6123ae60"
                  className="shinyButtonUI lightPurpleButton"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  Add LP
                </Button>
              </div>
            </div>
          </div>
        </Grid>
        <Grid item xs={12} sm={6}>
          <div className="tokenCard singldPD">
            <div className="cardContainer">
              <div className="tokenBox">
                <TokenSymbol symbol="POLYDOGE" />
                <h2>PolyDoge Single Staking Pool</h2>
                <span className="singleStakingPool purplePool">
                  Deposit POLYDOGE Earn PDShare
                  <Button href="/farm" className="shinyButtonUI lightPurpleButton">
                    {' '}
                    Stake PolyDoge
                  </Button>
                </span>
              </div>
            </div>
          </div>
        </Grid>
      </Grid>
    </Page>
  );
};

export default Home;
