/* eslint-disable */
import { useEffect, useState } from 'react';
import usePolyDogeFinance from './usePolyDogeFinance';
import { TokenStat } from '../polydoge-finance/types';
import useRefresh from './useRefresh';

const usePolyDogestats = () => {
  const [stat, setStat] = useState<TokenStat>();
  const { slowRefresh } = useRefresh();
  const polydogeFinance = usePolyDogeFinance();

  //1. Import coingecko-api
  const CoinGecko = require('coingecko-api');

  //2. Initiate the CoinGecko API Client
  const CoinGeckoClient = new CoinGecko();

  useEffect(() => {
    async function fetchPolyDogePrice() {
      try {
        let polydogePrice = await CoinGeckoClient.simple.price({
          ids: ['polydoge'],
          vs_currencies: ['usd'],
        });

        let polyPriceA = polydogePrice.data.polydoge.usd;

        // console.log('poly', polydogePrice.data.polydoge.usd);
        // console.log('polyA', polyPriceA);
      } catch (err) {
        console.error(err);
      }
    }
    fetchPolyDogePrice();
  }, [setStat, polydogeFinance, slowRefresh, CoinGeckoClient.simple]);

  return stat;
};

export default usePolyDogestats;
